@import '~@yandex-music-int/sol/mixins/breakpoints';

.root {
    display: flex;
    position: relative;
    inset-block-start: 0;
    inset-inline-end: 0;
    inset-inline-start: 0;

    z-index: var(--ym-z-index-header);

    justify-content: space-between;

    background-color: transparent;

    padding-block-start: var(--ym-spacer-size-l);
    padding-block-end: var(--ym-spacer-size-l);
    padding-inline-start: var(--ym-spacer-size-xxl);
    padding-inline-end: var(--ym-spacer-size-xxxl);

    height: var(--header-height);

    @include mediaBreakpointDown($width: 1024) {
        padding-inline-start: var(--ym-spacer-size-l);
        padding-inline-end: var(--ym-spacer-size-l);
    }
}

.action {
    margin-inline-end: var(--ym-spacer-size-xs);
}
