@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';

.root {
    display: flex;
    align-items: flex-start;

    width: 100%;

    gap: var(--ym-spacer-size-xxl);

    @include mediaBreakpointDown($ym-breakpoint-s) {
        flex-direction: column;
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        align-items: center;
        gap: var(--ym-spacer-size-l);
    }
}

.coverCell {
    display: block;
    flex-grow: 0;
    flex-shrink: 0;

    width: 100%;
    max-width: 204px;
    max-height: 204px;

    @include aspectRatio(1, 1);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        max-width: 168px;
        max-height: 168px;
    }
}

.coverImage {
    width: 100%;
    height: 100%;

    @include aspectRatio(1, 1);
}

.content {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;

    padding-block-start: var(--ym-spacer-size-xs);
    padding-block-end: var(--ym-spacer-size-l);
    padding-inline-start: var(--ym-spacer-size-xxxs);

    max-width: 100%;

    overflow: hidden;

    @include mediaBreakpointDown($ym-breakpoint-s, false) {
        padding: 0;
    }
}

.info {
    display: flex;
    flex-direction: column;

    width: 100%;

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        gap: var(--ym-spacer-size-m);
    }
}

.entityName {
    color: var(--ym-controls-color-primary-text-enabled);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        display: none;
    }
}

.title {
    margin-block-end: var(--ym-spacer-size-xs);

    max-width: 100%;

    color: var(--ym-controls-color-primary-text-enabled_variant);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        /* stylelint-disable-next-line value-no-vendor-prefix */
        display: -webkit-box;

        overflow: hidden;

        text-align: center;
        -webkit-line-clamp: 2;

        text-overflow: ellipsis;

        /*! autoprefixer: ignore next */
        -webkit-box-orient: vertical;
    }
}

.meta {
    margin-block-end: var(--ym-spacer-size-xxxl);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        margin-block-end: 0;
    }
}

.controls {
    display: flex;

    width: 100%;
    gap: var(--ym-spacer-size-m);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        justify-content: center;

        margin-block-start: var(--ym-spacer-size-xxl);
    }
}

@mixin border-gradient($from, $to) {
    /* stylelint-disable-next-line function-no-unknown */
    $main: mix($from, $to);
    /* stylelint-disable-next-line function-no-unknown */
    $sub-from: mix($main, $from);
    /* stylelint-disable-next-line function-no-unknown */
    $sub-to: mix($main, $to);

    /* stylelint-disable-next-line declaration-property-value-no-unknown */
    box-shadow: 0 4px 0 0 rgba($sub-to, 0.25), 0 -4px 0 0 rgba($sub-from, 0.25), 4px 0 0 0 rgba($sub-to, 0.25),
        -4px 0 0 0 rgba($sub-from, 0.25), 4px -4px 0 0 rgba($main, 0.5), -4px 4px 0 0 rgba($main, 0.5),
        4px 4px 0 0 rgba($to, 0.75), -4px -4px 0 0 rgba($from, 0.75);
}

.rewindLink {
    position: relative;

    transition: transform var(--ym-duration-transition);

    padding: var(--ym-spacer-size-xxxs);

    min-width: 204px;
    min-height: 204px;

    overflow: visible;

    aspect-ratio: auto;

    &::before {
        position: absolute;

        animation: rotate 5s linear infinite;

        border-radius: var(--ym-radius-size-round);

        width: 100%;
        height: 100%;

        inset-block-start: 0;
        inset-inline-start: 0;

        content: '';

        @include border-gradient(hsla(271deg, 91%, 56%, 1), rgb(228, 211, 19));

        @media screen and (prefers-reduced-motion: reduce) {
            animation: none;
        }
    }

    .coverImage {
        border-radius: var(--ym-radius-size-round);
    }

    &:hover,
    &:focus-visible {
        transform: scale(1.02);
    }

    @media screen and (prefers-reduced-motion: reduce) {
        transition: none;
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        min-width: 168px;
        min-height: 168px;
    }
}

@keyframes rotate {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}
