.rootOld {
    position: relative;

    border: 1px solid var(--ym-outline-color-primary-disabled);
    border-radius: var(--ym-radius-size-xl);

    background-color: var(--ym-background-color-primary-enabled-content);

    overflow: hidden;
}

.root {
    --content-transition-duration: 300ms;
    position: relative;
}

.primary {
    position: absolute;
    inset-inline-start: 50%;

    transform: translateX(-50%);

    // stylelint-disable-next-line plugin/no-low-performance-animation-properties
    transition: width var(--content-transition-duration) ease-in-out,
        filter var(--content-transition-duration) ease-in-out;

    border: 1px solid var(--ym-outline-color-primary-disabled);
    border-radius: var(--ym-radius-size-xl);

    background-color: var(--ym-background-color-primary-enabled-content);

    width: 100%;
    height: 100%;
    overflow-y: hidden;

    &_isMobile {
        border: none;
        border-radius: 0;
    }

    &_short {
        filter: brightness(1.5);

        width: 95%;
    }

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }
}

.secondary {
    position: absolute;

    z-index: 2;

    border: 1px solid var(--ym-outline-color-primary-disabled);
    border-radius: var(--ym-radius-size-xl);

    background-color: var(--ym-background-color-primary-enabled-content);
    padding-block-start: var(--header-height);

    width: 100%;
    height: calc(100% - var(--ym-spacer-size-l));
    inset-block-start: var(--ym-spacer-size-l);
    overflow-y: hidden;

    &_isMobile {
        border: none;
        border-radius: 0;

        height: 100%;
        inset-block-start: 0;
    }
}

.secondaryEnter {
    inset-block-start: 20%;

    opacity: 0;
}

.secondaryEnterActive {
    inset-block-start: var(--ym-spacer-size-l);

    transition: inset-block-start var(--content-transition-duration) ease-in-out,
        opacity var(--content-transition-duration) ease-in-out;

    opacity: 1;

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }
}

.secondaryExit {
    inset-block-start: var(--ym-spacer-size-l);
}

.secondaryExitActive {
    transform: scale(1.1);

    transition: opacity var(--content-transition-duration) ease-in-out,
        transform var(--content-transition-duration) ease-in-out;

    opacity: 0;

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }
}
