@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/focusOutline.scss';

.meta {
    color: var(--ym-controls-color-primary-text-enabled);
    font-weight: var(--ym-font-weight-medium);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: var(--ym-spacer-size-xxs);
    }
}

.artistLabel,
.year {
    line-height: var(--ym-font-line-height-label-m);
    letter-spacing: var(--ym-letter-spacing-m);
    color: var(--ym-controls-color-primary-text-enabled);
    font-size: var(--ym-font-size-label-m);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        flex-shrink: 0;
    }
}

.artists {
    color: var(--ym-controls-color-primary-text-enabled);

    @include mediaBreakpointUp($ym-breakpoint-xxs) {
        display: contents;
    }
}

.artistCover {
    display: inline-flex;

    position: relative;
    inset-block-start: var(--ym-spacer-size-xxxs);

    margin-inline-end: 6px;

    width: 20px;
    height: 20px;

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        inset-block-start: 0;

        flex-grow: 0;
        flex-shrink: 0;

        margin-block-start: 0;
    }
}

.artistsSpoiler {
    color: var(--ym-controls-color-primary-text-enabled_variant);

    &:focus,
    &:hover {
        color: var(--ym-controls-color-primary-text-hovered);
    }
}

.artistLink:hover .artistLabel,
.artistLink:focus .artistLabel {
    color: var(--ym-controls-color-primary-text-hovered);
}

.year {
    display: inline-block;

    &_dot::before {
        display: inline-block;

        margin-block-end: var(--ym-spacer-size-xxxs);
        margin-inline-start: var(--ym-spacer-size-m);
        margin-inline-end: var(--ym-spacer-size-m);

        border-radius: var(--ym-radius-size-round);
        background-color: var(--ym-controls-color-primary-text-enabled);

        width: 4px;
        height: 4px;

        content: '';
    }
}

.playButton {
    &_mobile {
        background-color: var(--ym-controls-color-primary-on_default-enabled);

        color: var(--ym-controls-color-primary-default-enabled);

        &:not(:disabled) {
            &:hover,
            &:focus-visible {
                background-color: var(--ym-controls-color-primary-on_default-enabled);

                color: var(--ym-controls-color-primary-default-hovered);
            }

            &:focus-visible {
                @include focusOutline(var(--ym-controls-color-primary-default-focused_stroke));
            }
        }
    }
}

.controls {
    display: flex;
    align-items: center;

    &_mobile {
        column-gap: var(--ym-spacer-size-xl);
    }

    &_desktop {
        column-gap: var(--ym-spacer-size-m);
    }
}
