@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    display: flex;
    flex-direction: column;

    padding: var(--ym-spacer-size-xxl);

    height: 100dvh;

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding: var(--ym-spacer-size-l);
    }
}

.navigation {
    padding-block-end: var(--ym-spacer-size-xl);
}

.content {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    align-items: center;
    justify-content: center;
}

.icon {
    margin-block-end: var(--ym-spacer-size-xxxl);

    color: var(--ym-controls-color-primary-default-enabled);
}

.title {
    text-align: center;

    color: var(--ym-controls-color-primary-text-enabled_variant);

    // TODO: убрать дополнительный селектор после фикса https://st.yandex-team.ru/MFRONT-5960
    &.important {
        margin-block-end: var(--ym-spacer-size-xs);
    }
}

.text {
    text-align: center;

    // TODO: убрать дополнительный селектор после фикса https://st.yandex-team.ru/MFRONT-5960
    &.important {
        padding-block-end: var(--ym-spacer-size-l);
    }
}

.button {
    min-width: 118px;
}
