@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';

.root {
    display: flex;

    width: 100%;

    gap: var(--ym-spacer-size-xxl);

    @include mediaBreakpointDown($ym-breakpoint-s) {
        flex-direction: column;
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        align-items: center;
        gap: var(--ym-spacer-size-l);
    }
}

.cover {
    display: block;
    flex-grow: 0;
    flex-shrink: 0;

    max-width: 204px;

    @include aspectRatio(1, 1);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        width: 100%;
        max-width: 168px;
    }
}

.content {
    display: flex;
    flex-direction: column;
    gap: var(--ym-spacer-size-xl);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding: 0;
    }
}

.info {
    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.entityName {
    margin-block-start: 8px;
    margin-block-end: 20px;

    width: 74px;
    height: 9px;

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        display: none;
    }
}

.title {
    margin-block-end: 20px;

    width: 365px;
    max-width: 90%;
    height: 29px;

    font-family: var(--ym-font-heading);
}

.meta {
    margin-block-end: var(--ym-spacer-size-xxl);

    width: 114px;
    height: 14px;
}

.controls {
    display: flex;

    width: 100%;

    gap: var(--ym-spacer-size-m);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        justify-content: center;

        margin-block-start: var(--ym-spacer-size-xxl);
    }
}

.desktopPlayButton {
    border-radius: var(--ym-radius-size-xxl);

    width: 112px;
    height: 40px;
}

.button {
    width: 40px;
    height: 40px;
}
