@import '~@yandex-music-int/sol/mixins/focusOutline.scss';
@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
}

.playButton {
    &_mobile {
        background-color: var(--ym-controls-color-primary-on_default-enabled);

        color: var(--ym-controls-color-primary-default-enabled);

        &:not(:disabled) {
            &:hover,
            &:focus-visible {
                background-color: var(--ym-controls-color-primary-on_default-enabled);

                color: var(--ym-controls-color-primary-default-hovered);
            }

            &:focus-visible {
                @include focusOutline(var(--ym-controls-color-primary-default-focused_stroke));
            }
        }
    }
}

.controls {
    display: flex;
    align-items: center;

    &_mobile {
        column-gap: var(--ym-spacer-size-xl);
    }

    &_desktop {
        column-gap: var(--ym-spacer-size-m);
    }
}

.meta {
    display: flex;
    align-items: center;

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        justify-content: center;
    }
}

.listeners {
    display: flex;
    align-items: center;
    gap: var(--ym-spacer-size-xxxs);

    border-radius: var(--ym-radius-size-xxxl);
    background-color: var(--ym-controls-color-secondary-default-enabled);

    padding-block-start: var(--ym-spacer-size-xxxs);
    padding-block-end: var(--ym-spacer-size-xxxs);
    padding-inline-start: var(--ym-spacer-size-xxxs);
    padding-inline-end: var(--ym-spacer-size-s);

    color: var(--ym-controls-color-primary-text-enabled_variant);
}
